import { Component, TemplateRef, ViewChild, ElementRef } from '@angular/core';
import { SeriesTooltipTemplateDirective } from './series-tooltip-template.directive';
import { SharedTooltipTemplateDirective } from './shared-tooltip-template.directive';
import { TooltipTemplatePoint } from './tooltip-template-point';
import { BaseTooltip } from './base-tooltip';
import { hasParent } from '../../common/has-parent';
import { TooltipTemplateService } from '../../common/tooltip-template.service';

const SHARED_TOOLTIP_CLASS = 'k-chart-shared-tooltip';

/**
 * @hidden
 */
@Component({
  selector: 'kendo-chart-tooltip-popup',
  template: `
    <kendo-popup [offset]="offset" [popupAlign]="align" [collision]="collision" *ngIf="active" >
        <div [ngClass]="popupClasses" [ngStyle]="style">
          <template [ngTemplateOutlet]="seriesTooltipTemplateRef" *ngIf="!shared"
                    [ngOutletContext]="seriesTooltipContext">
          </template>
          <template [ngTemplateOutlet]="seriesSharedTooltipTemplateRef" *ngIf="shared"
                    [ngOutletContext]="seriesSharedTooltipContext">
          </template>
        </div>
    </kendo-popup>
    <template kendoSeriesTooltipTemplate let-formattedValue="formattedValue">
        <span [innerHTML]="formattedValue"></span>
    </template>
    <template kendoSharedTooltipTemplate let-points="points" let-categoryText="categoryText">
        <table>
            <tr><th colspan='3'> {{ categoryText }} </th></tr>
            <tr *ngFor="let point of points">
                <td><span class='k-chart-shared-tooltip-marker' [style.background-color]='point.series.color'></span></td>
                <td *ngIf="point.series.name"> {{ point.series.name }}</td>
                <td>
                  <template [ngTemplateOutlet]="point.template"
                            [ngOutletContext]="point">
                  </template>
                </td>
            </tr>
        </table>
    </template>
    `
})
export class TooltipPopupComponent extends BaseTooltip {
  public seriesTooltipTemplateRef: TemplateRef<any>;
  public seriesSharedTooltipTemplateRef: TemplateRef<any>;
  public seriesTooltipContext: any = {};
  public seriesSharedTooltipContext: any = {};
  protected shared: boolean;

  @ViewChild(SeriesTooltipTemplateDirective)
  protected defaultSeriesTooltipTemplate: SeriesTooltipTemplateDirective;

  @ViewChild(SharedTooltipTemplateDirective)
  protected defaultSharedTooltipTemplate: SharedTooltipTemplateDirective;

  constructor(private element: ElementRef,
              private templateService: TooltipTemplateService) {
    super();
  }

  public show(e: any): void {
    super.show(e);

    this.shared = e.shared;
    this.popupClasses[SHARED_TOOLTIP_CLASS] = e.shared;

    if (!e.shared) {
        this.seriesTooltipContext = new TooltipTemplatePoint(e.point, e.format);
        this.seriesTooltipTemplateRef = this.pointTemplateRef(e.point);
    } else {
        this.seriesSharedTooltipTemplateRef = this.templateService.getSharedTemplate()
            || this.defaultSharedTooltipTemplate.templateRef;
        this.seriesSharedTooltipContext = {
            category: e.category,
            categoryText: e.categoryText,
            points: this.wrapPoints(e.points, e.format)
        };
    }
  }

  public containsElement(element: any): boolean {
    const tooltipElement = this.element.nativeElement;
    return hasParent(element, tooltipElement);
  }

  protected pointTemplateRef(point: any): TemplateRef<any> {
    return this.templateService.getTemplate(point.series.index) || this.defaultSeriesTooltipTemplate.templateRef;
  }

  protected wrapPoints(points: Array<any>, format?: string): Array<TooltipTemplatePoint> {
      const result: Array<TooltipTemplatePoint> = [];

      for (let idx = 0; idx < points.length; idx++) {
          const point: any = points[idx];
          const template: TemplateRef<any> = this.pointTemplateRef(point);
          result.push(new TooltipTemplatePoint(point, format, template));
      }

      return result;
  }
}



